import "./index.scss"
import React, { Component } from "react"
// import $ from "jquery"
import { Icon, Tooltip, Popover, Modal, Menu, Dropdown } from "antd"
import SvgIcons from "Components/svg_icons"
const { WeChatIcon, GithubCircle } = SvgIcons
export default class Header extends Component {
  constructor(props) {
    super(props)
    this.state = {
      starCount: 0,
      helpList: [
        // {
        //   name: "关于开源",
        //   url: "./faq.html?tab=2",
        //   target: "_blank"
        // },
        {
          name: "问/答(FAQ)",
          url: "./faq.html",
          target: "_blank"
        },
        {
          name: "API",
          url: "./api.html",
          target: "_blank"
        }
      ]
    }
  }

  componentDidMount() {
    document.getElementById("progress_bar").style.display = "none"

    $(window).scroll(() => {
      const top = $(document).scrollTop()
      if (top > 30) {
        $(".headerHome-container").fadeOut()
      } else if (top < 20) {
        $(".headerHome-container").fadeIn()
      }
    })
  }
  render() {
    const { helpList } = this.state
    const url = window.location.href.split("?")[0]
    // 扫码添加微信群
    const scanQrCode = <div className="qrcode-container">
      <img src={require("Images/wx_add.jpeg")}/>
    </div>

    const helpInfoMenu =
      <Menu>
        {
          helpList.map((menuName, index) => {
            return <Menu.Item key={ index }>
              <a className="userInfo-box" target={menuName.target} href={menuName.url}>
                {menuName.icon} <div className="item-name"><span>{menuName.name}</span></div>
              </a>
            </Menu.Item>
          })
        }
      </Menu>
    return <div className="headerHome-container">
      <section className="sub-header">
        <span className="home-icon" onClick={this.turnToHome.bind(this)}>
          <img style={{width: 200}} src={require("Images/logo2.png")}/>
        </span>
        <div className="menu-container">
          <span className="menu-right menu-short">
            <Tooltip placement="topLeft" title="">
              <Popover placement="topLeft" title={"扫码添加作者微信"} content={scanQrCode} trigger="click">
                <Icon component={WeChatIcon}/>
              </Popover>
            </Tooltip>
          </span>
          <a style={{color: url.indexOf("home.html") !== -1 ? "#ea6847" : "#333"}} className="menu-right menu-short" onClick={this.turnToQuickStart.bind(this, "home.html")}>功能</a>
          <a style={{color: url.indexOf("des.html") !== -1 ? "#ea6847" : "#333"}} className="menu-right menu-short" href="./des.html">文档</a>
          <a style={{color: "#333"}} className="menu-right menu-short" target="_blank" href="http://www.webfunny.cn/demo/home.html">Demo</a>
          <a style={{color: "#333"}} className="menu-right menu-short" target="_blank" href="https://github.com/a597873885/webfunny_monitor"><Icon component={GithubCircle}/></a>
          <span className="menu-right">
            <Dropdown overlay={helpInfoMenu} trigger={["hover"]}>
              <a className="ant-dropdown-link">
                帮 助 <Icon type="down" />
              </a>
            </Dropdown>
          </span>
        </div>
      </section>
    </div>
  }
  getSourceCode() {
    Modal.confirm({
      title: "友情提示",
      content: "源码需要付费，有意者请加我的微信私聊。 请扫码或者搜索微信号 webfunny_2020",
      okText: "我知道了",
      cancelText: "取消",
      iconType: "warning",
      onOk: () => {}
    })
  }
  turnToQuickStart(page) {
    window.location.href = "./" + page
  }
  turnToHome() {
    window.open("http://www.webfunny.cn/site/home.html")
  }
  turnToBlog() {
    window.open("https://github.com/a597873885/webfunny_monitor")
  }
  turnTo(url) {
    if (!url) return
    window.location.href = "./" + url
  }
}
